import { cast, flow, getEnv, Instance, types, getRoot } from 'mobx-state-tree';

import { HttpErrorException, HttpResponseStatusCode } from '@yandex-music-int/http-client';
import {
    ArtistsResourceBriefInfoParams,
    ArtistsResourceBriefInfoResponse
} from '@yandex-music-int/resource-collection';

import { AlbumWithArtists, albumWithArtistsResponseToModel } from '@/features/album/index.js';
import { ArtistMeta } from '@/features/artist/index.js';
import { playlistResponseToMetaModel, PlaylistWithArtists } from '@/features/playlist/index.js';
import { TrackWithArtists, trackWithArtistsResponseToModel } from '@/features/track/index.js';

import { Artist as ArtistEntity, artistResponseToModel } from '@/entities/artist/index.js';

import { LoadingState, StoreEnv, Experiments } from '@/shared/lib/index.js';

import { artistBriefInfoResponseToMeta } from '../lib/artistBriefInfoResponseToMeta.js';
import { concatPopularAlbums } from '../lib/concatPopularAlbums.js';

const POPULAR_TRACKS_COUNT = 5;
const SIMILAR_ARTISTS_COUNT = 9;
const POPULAR_ALBUMS_COUNT = 9;

export const Artist = types
    .model('Artist', {
        loadingState: types.enumeration(Object.values(LoadingState)),
        meta: types.maybeNull(ArtistMeta),
        popularTracks: types.array(TrackWithArtists),
        lastRelease: types.maybeNull(AlbumWithArtists),
        similarArtists: types.array(ArtistEntity),
        popularAlbums: types.array(AlbumWithArtists),
        playlists: types.array(PlaylistWithArtists),
        errorStatusCode: types.maybeNull(types.number)
    })
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getData: flow(function* ({
                artistId
            }: ArtistsResourceBriefInfoParams): Generator<
                Promise<ArtistsResourceBriefInfoResponse>,
                void,
                ArtistsResourceBriefInfoResponse
            > {
                // @ts-ignore
                const { slides, experiments } = getRoot(self);
                const { artistsResource, logger } = getEnv<StoreEnv>(self);
                const isLastReleasesDisabled = experiments.checkExperiment(
                    Experiments.WebNextArtistDisableLastReleases,
                    'on'
                );
                const isPopularTracksDisabled = experiments.checkExperiment(
                    Experiments.WebNextArtistDisablePopularTracks,
                    'on'
                );
                const isSimilarArtistsDisabled = experiments.checkExperiment(
                    Experiments.WebNextArtistDisableSimilarArtists,
                    'on'
                );
                const isPopularAlbumsDisabled = experiments.checkExperiment(
                    Experiments.WebNextArtistDisablePopularAlbums,
                    'on'
                );
                const isPlaylistsDisabled = experiments.checkExperiment(
                    Experiments.WebNextArtistDisablePlaylists,
                    'on'
                );

                if (self.loadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    self.loadingState = LoadingState.PENDING;

                    const {
                        popularTracks,
                        error,
                        artist,
                        stats,
                        albums,
                        playlists,
                        alsoAlbums,
                        similarArtists,
                        lastReleases
                    } = yield artistsResource.getBriefInfo({
                        artistId,
                        popularTracksCount: POPULAR_TRACKS_COUNT,
                        fetchPlaylistLikesCounts: true
                    });

                    if (error === 'not-found' || artist?.error === 'not-found') {
                        self.errorStatusCode = HttpResponseStatusCode.NOT_FOUND;
                        self.loadingState = LoadingState.REJECT;
                        return;
                    }

                    self.meta = cast(artistBriefInfoResponseToMeta(artist, stats));

                    if (!isLastReleasesDisabled && lastReleases && lastReleases.length > 0) {
                        const lastRelease = lastReleases[0];

                        if (lastRelease && albums && albums.length > 1) {
                            self.lastRelease = cast(albumWithArtistsResponseToModel(lastRelease));
                        }
                    }

                    if (!isPopularTracksDisabled && popularTracks && popularTracks.length > 0) {
                        self.popularTracks = cast(
                            popularTracks.slice(0, POPULAR_TRACKS_COUNT).map(trackWithArtistsResponseToModel)
                        );
                    }

                    if (!isSimilarArtistsDisabled && similarArtists && similarArtists.length > 0) {
                        self.similarArtists = cast(
                            similarArtists.slice(0, SIMILAR_ARTISTS_COUNT).map(artistResponseToModel)
                        );
                    }

                    if (!isPopularAlbumsDisabled) {
                        self.popularAlbums = cast(concatPopularAlbums(albums, alsoAlbums, POPULAR_ALBUMS_COUNT));
                    }

                    if (!isPlaylistsDisabled && playlists && playlists.length > 0) {
                        self.playlists = cast(playlists.map(playlistResponseToMetaModel));
                    }

                    slides.artistReset();
                    if (slides.artistSlidesLoadingState === LoadingState.IDLE) {
                        yield slides.getArtistSlides({
                            artistId
                        });
                    }

                    // @ts-expect-error
                    // TS считает, что статусы никогда не пересекутся, но на самом деле мы можем уйти со страницы,
                    // раньше чем закончится последний ретрай и/или запрос, когда уже проставлен статус IDLE
                    if (self.loadingState !== LoadingState.IDLE) {
                        self.loadingState = LoadingState.RESOLVE;
                    }
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    if (
                        error instanceof HttpErrorException &&
                        (error.statusCode === HttpResponseStatusCode.NOT_FOUND ||
                            error.statusCode === HttpResponseStatusCode.BAD_REQUEST)
                    ) {
                        self.errorStatusCode = HttpResponseStatusCode.NOT_FOUND;
                    }

                    if (self.loadingState !== LoadingState.IDLE) {
                        self.loadingState = LoadingState.REJECT;
                    }
                }
            }),
            reset() {
                self.loadingState = LoadingState.IDLE;
                self.meta = null;
                self.errorStatusCode = null;
                self.lastRelease = null;
                self.popularTracks = cast([]);
                self.similarArtists = cast([]);
                self.playlists = cast([]);
            }
        };
    })
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => {
        return {
            get isLoading(): boolean {
                return self.loadingState === LoadingState.IDLE || self.loadingState === LoadingState.PENDING;
            },

            get isPopularTracksExist(): boolean {
                return (
                    self.loadingState === LoadingState.IDLE ||
                    self.loadingState === LoadingState.PENDING ||
                    self.popularTracks.length > 0
                );
            },

            get popularAlbumsTitle(): string {
                const counts = self.meta?.artist?.counts;

                if (counts && counts.albums > 0 && counts.albums < POPULAR_ALBUMS_COUNT && counts.compilations > 0) {
                    return 'Популярные альбомы и сборники';
                } else if (counts && counts.albums > 0) {
                    return 'Популярные альбомы';
                }

                return 'Популярные сборники';
            },

            get isPopularAlbumsExists(): boolean {
                return (
                    self.loadingState === LoadingState.IDLE ||
                    self.loadingState === LoadingState.PENDING ||
                    self.popularAlbums.length > 0
                );
            },

            get isSimilarArtistsExist(): boolean {
                return (
                    self.loadingState === LoadingState.IDLE ||
                    self.loadingState === LoadingState.PENDING ||
                    self.similarArtists.length > 0
                );
            },

            get isPlaylistsExist() {
                return (
                    self.loadingState === LoadingState.IDLE ||
                    self.loadingState === LoadingState.PENDING ||
                    (self.playlists && self.playlists.length > 0)
                );
            },

            get isLastReleaseExists(): boolean {
                return (
                    self.loadingState === LoadingState.IDLE ||
                    self.loadingState === LoadingState.PENDING ||
                    Boolean(self.lastRelease)
                );
            },

            get isNotFound(): boolean {
                return (
                    self.loadingState === LoadingState.REJECT &&
                    self.errorStatusCode === HttpResponseStatusCode.NOT_FOUND
                );
            }
        };
    });

export type ArtistModel = Instance<typeof Artist>;
