import { Album } from '@yandex-music-int/resource-collection';

import { AlbumWithArtistsModel, albumWithArtistsResponseToModel } from '@/features/album/index.js';

export const concatPopularAlbums = (
    albums: Album[] = [],
    alsoAlbums: Album[] = [],
    count: number
): AlbumWithArtistsModel[] => {
    let popularAlbums: Album[] = albums;

    if (albums.length < count) {
        popularAlbums = popularAlbums.concat(alsoAlbums);
    }

    return popularAlbums.map(albumWithArtistsResponseToModel).slice(0, count);
};
