import { cast } from 'mobx-state-tree';

import { Artist } from '@yandex-music-int/resource-collection';

import { ArtistMetaModel } from '@/features/artist/index.js';

import { artistResponseToModel } from '@/entities/artist/index.js';

export const artistBriefInfoResponseToMeta = (
    artist: Artist,
    stats?: { lastMonthListeners: number }
): ArtistMetaModel => {
    return cast({
        artist: artistResponseToModel(artist),
        lastMonthListeners: stats?.lastMonthListeners
    });
};
