'use client';

import { notFound, useSearchParams } from 'next/navigation.js';
import { ReactElement } from 'react';

import { SecondaryPage } from '@/app/navigation/index.js';

import { ArtistPage } from '@/pages/artist/index.js';

const Artist = (): ReactElement => {
    const searchParams = useSearchParams();

    const artistId = searchParams.get('artistId');

    if (!artistId) {
        notFound();
    }

    return (
        <SecondaryPage>
            <ArtistPage artistId={artistId} />
        </SecondaryPage>
    );
};

// eslint-disable-next-line import/no-default-export
export default Artist;
